    /**  */
    yes = '<span class="f-icon f-icon-star f-lookup-favorite" style="line-height: 29px;"></span>',
    /**  */
    no = '<span class="f-icon f-icon-star-outline f-lookup-favorite" style="line-height: 29px;"></span>',
