export interface ExtInfoFormatterParam {
    [key: string]: any;
    bindingData?: any;
    instance?: any;
}

export type ExtInfoFormatterFn = (param: ExtInfoFormatterParam) => string;
export type ExtInfoFormatter = string | ExtInfoFormatterFn;

export interface CustomDataFormatter {
    [fieldName: string]: any ;
    rowStyler?: any;
    cellStyler?: any;
}

export interface QuickSelectOption {
    /**  */
    enable?: boolean;
    /** top  10 */
    showItemsCount?: number;
    /**  */
    textField?: string;
    /**  */
    formatter?: (data: any) => string;
    
    footerHeight?: number;
    /**   */
    showMore?: boolean;
}

export const QuickSelectDefaultOptions: QuickSelectOption = {
    enable: false,
    showMore: true,
    showItemsCount: 10,
    footerHeight: 0
}